// 
$breakpoint-md: 768px;
$breakpoint-lg: 1024px;
$transition-duration: 0.5s;
$border-radius: 1.38rem; //   12px   

.page-tariffs {
  & .container-fluid {
    margin: 0 auto;
  }

  & .modal-submit-form {

    .btn-close {
      & svg {
        display: none;
      }

      &::before {
        font-size: 250%;
        color: rgba(0, 0, 0, .25);
        background-color: transparent;
        left: -30%;
      }
    }

    & .modal-content {
      border-radius: 0.75rem;
      max-width: 400px;
      margin: 0 auto;
    }

    & .modal-body {
      padding-top: 2rem;
      font-weight: 700;
      font-size: 0.75rem;
      line-height: 100%;
      text-align: center;

      border-radius: 0.75rem;
      box-shadow: 0 4px 11px 0 rgba(0, 0, 0, 0.25);
      background: linear-gradient(169deg, #f5f7f8 0%, #e1ebf0 100%);

        & svg {
          z-index: 0;
          width: 36px;
          height: 36px;
          fill: #e1ebf0;
        }

    }

    & .form-message-modal {
    padding-bottom: 0;
    }

    & .form-message-modal__title {
      padding: 1rem;
      margin-top: -1rem;
      font-weight: 700;
      font-size: 0.88rem;
      line-height: 100%;
      text-align: center;
      border-radius: 0.38rem;
      border: 2px solid #3080a3;
    }
    & .form-message-modal__text {
      padding: 1rem;
    }

    & .social {
      display: flex;
      flex-direction: row;
      justify-content: center;
      gap: 1rem;

      &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.5rem;

        & .icon {
          width: 30px;
          height: 30px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }


        &--vk {
          & .icon {
            background-image: url('../img/pages/club-tariffs/icons/icon-vk.svg');
          }
        }
        &--dz {
          & .icon {
            background-image: url('../img/pages/club-tariffs/icons/icon-dzen.svg');
          }
        }
        &--tm {
          & .icon {
            background-image: url('../img/pages/club-tariffs/icons/icon-telegram.svg');
          }
        }

        & a {
          display: block;
        }
      }
    }
  }

}

.tariff-tooltip, .trade-in-tooltip, .advantages-tooltip {
  &.tooltip.show {
    opacity: 1;
  }

  .tooltip-inner {
    background: white;
    padding: 0.5rem;
    box-shadow: 4px 4px 12px 0 rgba(66, 71, 77, 0.1);
    border-radius: 0.75rem;
    font-family: $mainFont;
    font-weight: 400;
    font-size: 0.81rem;
    line-height: 130%;
    color: #42474d;
    opacity: 1;
  }

  .tooltip-arrow::before {
    border-color: transparent;
    border-top-color: white;
  }
}
