.page-tariffs__club-tariffs {
  color: var(--text-color);
  background: var(--bg-color);


  & .club-tariffs {

    &__text {
      padding: 20px 1rem;
      font-size: 12px;
      line-height: 130%;
      letter-spacing: 0.02em;
      color: var(--text-color);
      max-width: 1000px;
      margin: 0 auto;

      @media (min-width: $md) {
        font-family: $mainFontBold;
        font-weight: 700;
        font-size: 16px;
        padding: 50px 1rem 50px 0;
        text-align: center;
      }
      @media (min-width: $md) {
        font-size: 24px;
        text-align: left;
        padding-left: 4rem;
      }

      @media (min-width: $xl) {
        padding-left: 5rem;
        max-width: 1225px;
      }

      @media (min-width: 1500px) {
        padding-left: 3rem;
      }

      @media (min-width: 1600px) {
        padding-left: 0;
      }

      & .link {
        color: var(--text-color);
      }
    }

    &__title {
      font-family: $mainFontBold;
      font-size: 24px;
      font-weight: 700;
      line-height: 130%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      text-align: center;
      margin: 0 auto 35px;
      padding-top: 50px;
      color: var(--text-color);

      @media (min-width: $md) {
        font-size: 28px;
      }
      @media (min-width: $md) {
        font-size: 40px;
        margin-bottom: 50px;
      }
    }
  }

    .slider-tariffs {
      padding: 0;
      position:relative;
      display: flex;
      justify-content: space-around;
      padding: 0 0 3rem 1rem;
      width: 100%;
      max-width: 1440px;
      margin: 0 auto;

      @media (min-width: $md) {
        padding: 0 0 5rem 3rem;
      }
      @media (min-width: $xxl) {
        padding: 0 5rem;
      }
    }

    .swiper-container {
      width: 100%;
      overflow: hidden;
    }

    .swiper-button-next {
      bottom: 0;
      top: auto;
      right: calc(50% - 30px);
      transform: translateX(50%);
    }

    .swiper-button-prev {
      bottom: 0;
      top: auto;
      left: calc(50% - 30px);
      transform: translateX(-50%);
    }

    .swiper-button-next,
    .swiper-button-prev {
      background: rgba(255, 255, 255, 0.9); //  
      width: 50px; //  
      height: 50px;
      border-radius: 50%;
      color: #2d3436; //  
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
      backdrop-filter: blur(5px);

      @media (max-width: $md) {
        display: none;
      }

      &::after {
        font-size: 18px; //  
        font-weight: bold;
      }

      &:hover {
        background: white;
        box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
      }

      &.swiper-button-disabled {
        opacity: 0.4;
        pointer-events: none;
      }
    }

    .swiper-pagination {
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);

      @media (min-width: $md) {
        display: none;
      }

      &-bullet {
        background: var(--text-color);
        opacity: 0.7;
        border-radius: 1.44rem;
        width: 1.06rem;
        height: 0.25rem;

        margin: 0 4px;
        transition: all 0.3s ease;

        &-active {
          opacity: 1;
          transform: scale(1.1); //   
          background: var(--text-color); //   
        }
      }
    }

    //      
    &.all-visible {
      .swiper-button-next,
      .swiper-button-prev,
      .swiper-pagination {
        display: none;
      }
    }

  @media (max-width: 1023px) {
    .swiper-container {
      width: auto;
    }
  }
}
