
.section-title {
  padding: 35px 25px 20px;

  @media (min-width: $sm) {
    padding: 35px 55px 30px;
  }

  @media (min-width: $md) {
    padding: 50px;
  }

  & .title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1.25rem;
    line-height: 120%;
    text-transform: uppercase;
    text-align: center;
    color: #4b5052;

    &--dark {
      color: #42474d;
    }

    @media (min-width: $sm) {
      font-size: 1.75rem;
    }

    @media (min-width: $md) {
      font-size: 2.5rem;
    }
  }
}
