.tariffs-header {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0;

  @media (min-width: $xxl) {
    overflow: hidden;
  }

  @media (min-width: $xxlg) {
    min-height: 800px;
  }

  &__top {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    max-width: 1440px;

    font-family: $mainFontBold2;
    font-weight: 750;
    font-size: 1.69rem;
    line-height: 130%;
    letter-spacing: 0.02em;
    color: #4b5052;
    padding: 20px 1rem 40px;

    @media (min-width: $sm) {
      gap: 10px;
      padding-bottom: 36px;
    }
    @media (min-width: $md) {
      flex-direction: row;
      flex-wrap: wrap;
    }
    @media (min-width: $xl) {
      margin: 0 auto;
      padding: 0 20px 40px 70px;
    }

    & .title {
      font-family: $mainFontBold2;
      font-weight: 750;
      font-size: 1.69rem;
      line-height: 130%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      max-width: 1100px;
      margin-bottom: 8px;

      @media (min-width: $sm) {
        font-size: 1.95rem;
        line-height: 120%;
      }

      @media (min-width: $md) {
        font-size: 4.19rem;
      }

      & span {
        @media (max-width: $xs) {
          display: block;
        }
      }
    }
  }

  & .link {
      overflow: hidden;
      position: relative;

      display: inline-flex;
      align-items: center;

      margin-right: auto;
      padding: 0.62rem 1rem 0.75rem;
      min-height: 56px;

      color: white;
      background: linear-gradient(176deg, #e9876c 0%, #da6240 76.4%, #db7255 100%);

      border-radius: 0.75rem;

      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 1.25rem;
      line-height: 130%;
      text-align: center;
      transition: all 0.4s ease;
      text-decoration: none;

      @media (min-width: $md) {
        margin-top: 8px;
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
        transition: left 0.5s ease;
      }

      &:hover {
        transform: translateY(-3px);
        box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);

        &::before {
          left: 100%;
        }
      }

      & svg {
        width: 6.5rem;
        height: 1.5rem;
        margin-left: 0.5rem;
      }
    }


  &__info {
    position:relative;
    width: 100%;
    height: 100%;
    min-height: 400px;
    max-height: 615px;
    background-color: rgb(36, 108, 190);

    @media (min-width: $xxlg) {
      max-height: 1000px;
    }
  }

  &__wrapper {
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 100%;
    background: linear-gradient(169deg, #f5f7f8 0%, #e1ebf0 100%);
    border-radius: 0.75rem;

    @media (min-width: $xl) {
      bottom: -1px;
      border-radius: 0;
    }
  }

  &__list {

    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
    max-width: 1440px;

    padding: 24px;
    padding-left: 48px;
    margin: 0;

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 0.88rem;
    line-height: 100%;

    color: #42474c;


    @media (min-width: $sm) {
      flex-direction: row;
      justify-content: space-between;

      min-height: 68px;
      padding: 1.5rem 2.5rem;
    }

    @media (min-width: $xl) {

      flex-direction: row;
      padding: 1.6rem 3rem;

      font-size: 1.5rem;
      border-radius: 0;
    }

  }
  &__img {
    width: 100%;
    height: auto;
    object-fit: cover;
    object-position: bottom;
    background: linear-gradient(171deg, #3485aa 18.3%, #1e6586 76.81%, #2e7292 100%);
  }
}

.tariffs-header__price {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  flex-wrap: wrap;
  margin-bottom: 8px;

  &-count {
    font-size: 1.5rem;
    font-weight: 750;
    line-height: 100%;

    @media (min-width: $sm) {
      gap: 10px;
      font-size: 1.75rem;
    }
    @media (min-width: $md) {
      font-size: 4.19rem;
    }
  }

  &-unit {
    font-size: 1.06rem;
    line-height: 120%;
    text-transform: uppercase;

    @media (min-width: $sm) {
      font-size: 1.25rem;
    }
    @media (min-width: $md) {
      font-size: 1.62rem;
    }
  }

}
