
.feedback-form {
  @media (min-width: $md) {
    padding: 0 50px !important;
  }

  &__wrap {
    max-width: 1280px;
    margin: 0 auto;
    backdrop-filter: blur(32px);
    background: linear-gradient(171deg, #3485aa 18.3%, #1e6586 76.81%, #2e7292 100%);
    padding: 1rem 0;
    overflow: hidden;

    @media (min-width: $xs) {
      border-radius: 1.5rem;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 50%;
        height: 100%;
        background-image: url("../img/pages/club-tariffs/bg-form.webp");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        border-top-right-radius: 1.5rem;
        border-bottom-right-radius: 1.5rem;
        overflow: hidden;
      }
    }

    @media (min-width: $xs) {
      padding: 1.5rem;
      padding-right: 0.5rem;
    }

    @media (min-width: $md) {
      padding: 35px 1rem;
      &::before {
        display: none;
      }
    }


  }

  & .container {
    max-width: 768px;
    margin: 0 auto;
    max-width: 500px;

    @media (min-width: $xs) {
      width: 50%;
      transform: translateX(50%);
    }

    @media (min-width: $md) {
      width: 100%;
      max-width: 650px;
      transform: translateX(0);
    }
  }

  & .list {
    display: flex;
    flex-direction: column;
    gap: 10px;
    font-family: $mainFontMedium;
    font-weight: 500;
    font-size: 1rem;
    line-height: 100%;
    letter-spacing: 0.02em;
    color: #fff;
    margin-bottom: 1rem;
    padding-left: 2rem;

    & li {
      position: relative;
      padding-left: 0.5rem;

      &::before {
        content: attr(data-li);
        position: absolute;
        top: 0;
        left: 0;
        transform: translateX(-100%);

        font-family: $mainFontBold;
        font-weight: 700;
        font-size: 1.12rem;
        line-height: 90%;
        letter-spacing: 0.02em;
        text-transform: uppercase;
      }
    }
  }

  & .button[type='submit'] {
    position: relative;
      display: block;
      cursor: pointer;
      text-align: center;
      margin-right: auto;
      border-radius: 0.75rem;
      padding: 0.62rem 1rem 0.75rem; //  

      color: white;
      font-weight: 700;
      font-size: 1.06rem;
      line-height: 130%;
      transition: all 0.4s ease;

      background: linear-gradient(176deg, #e9876c 0%, #da6240 76.4%, #db7255 100%);
      overflow: hidden;

      @media (max-width: $md) {
       box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.25);
      }

      @media (min-width: $md) {
        padding: 0.62rem 1.56rem 0.75rem;
      }

      @media (min-width: $xl) {
        padding: 0.75rem 1.5rem;
        font-size: 1.12rem;
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
        transition: left 0.5s ease;
      }

      &:hover {
        transform: translateY(-3px);
        box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);

        &::before {
          left: 100%;
        }
      }

  }

  & .form-personal {
    margin-bottom: 0.5rem;
    font-weight: 400;
    font-size: 0.81rem;
    line-height: 140%;
    color: #fff;
    // flex-grow: 1;

    @media (min-width: $md) {
      font-size: 0.69rem;
      max-width: 60%;
    }

    & a {
      color: inherit;
      text-decoration: underline;
      text-decoration-skip-ink: none;
    }
  }

  & .form-control,
  & .form-select {
    border-radius: 0.75rem;
  }

  & .form-control {
    padding: 0.62rem 1.56rem 0.75rem;
  }

  & .form-select {
    border-radius: 0.75rem;
    padding: 0.6rem 1.56rem 0.6rem;
  }

  & .form__btns {
    display: flex;
    gap: 10px;

    @media(min-width: $md) {
      flex-direction: row-reverse;

      align-items: center;
    }

    & .button {
      flex-grow: 1;

      @media(max-width: $sm) {
        width : 100%;
      }
    }
  }
}
