
.page-tariffs__map-clubs {
  margin-bottom: 2rem;
  & iframe {
    width: 100%;
    height: 400px;
    background: linear-gradient(171deg, #3485aa 18.3%, #1e6586 76.81%, #2e7292 100%);

    @media(min-width: $sm) {
      height: 500px;
    }
  }

}
.page-tariffs__map-club {
  & iframe {
    width: 100%;
    height: 300px;
    background: linear-gradient(171deg, #3485aa 18.3%, #1e6586 76.81%, #2e7292 100%);
  }
}

.map-title {
  font-family: $mainFontBold;
  font-weight: 700;
  font-size: 0.88rem;
  line-height: 140%;
  text-align: center;
  color: #42474c;

  padding: 30px;

  @media(min-width: $sm) {
    font-size: 1.62rem;
    padding: 40px 80px;
    line-height: 180%;
  }

  @media(min-width: $xl) {
    font-size: 3.25rem;
    padding: 100px 16px;
    line-height: 140%;
  }

  &__clubs {
    font-family: $mainFontBold2;
    font-weight: 750;
    font-size: 1.5rem;

    @media(min-width: $sm) {
      font-size: 3.19rem;
    }

    @media(min-width: $xl) {
      font-size: 5.25rem;
    }
  }

  &__city {
    font-family: $mainFont;
    font-size: 0.88rem;

    @media(min-width: $sm) {
      font-size: 1.62rem;
    }

    @media(min-width: $xl) {
      font-size: 2.5rem;
    }
  }

  &__city-dop {
    font-family: $mainFontBold2;
    font-size: 0.88rem;

    @media(min-width: $sm) {
      font-size: 3.19rem;
    }

    @media(min-width: $xl) {
      font-family: $mainFontBold;
      font-size: 3.25rem;
    }
  }

  &__link {
    text-decoration: underline;
  }
}

.map-sidebar {
  display: flex;
  flex-direction: column;
  gap: 5px;


  backdrop-filter: blur(20px);
  background: linear-gradient(169deg, #f5f7f8 0%, #e1ebf0 100%);
  color: #42474c;
  border-radius: 1rem 0 0 1rem;
  padding: 1.25rem 1.5rem;

  @media(min-width: $sm) {
    position: absolute;
    top: 0;
    right: 0;
    width: 300px;
    height: 98%;
  }

  @media(min-width: $md) {
    width: 400px;
  }

  &__club {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1rem;
    line-height: 110%;

    @media(min-width: $sm) {
      font-weight: 900;
      font-size: 1.38rem;
    }

    @media(min-width: $xl) {
      font-size: 1.62rem;
    }
  }
  &__address {
    margin: 0;
    font-weight: 500;
    font-size: 0.88rem;
    line-height: 129%;
  }

  &__link {
    font-weight: 500;
    font-size: 0.88rem;
    line-height: 129%;
    color: inherit;
    text-decoration: none;
  }

  &__work {
    margin-top: auto;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 0.88rem;
    line-height: 140%;
  }

  &__table {
    font-weight: 400;
    font-size: 0.75rem;
    line-height: 140%;
    text-transform: uppercase;
    border-collapse: collapse;
    margin-right: auto;

    & td {
      padding: 0.25rem 0.5rem;
    }
  }

  & .map-sidebar__td {
    display: flex;
    align-items: flex-end;
  }

  & .icon {
    display: inline-block;
    margin-right: 0.25rem;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }

  & .icon-clock {
    background-image: url('../img/pages/club-tariffs/icons/icon-clock.svg');

  }

  & .icon-user {
    background-image: url('../img/pages/club-tariffs/icons/icon-user.svg');
  }
}

.lazy-iframe {
  border: none;
  transition: opacity 0.3s ease;
}

.lazy-iframe:not([src]) {
  opacity: 0.7;
  background: #f5f5f5;
}

.lazy-iframe.loaded {
  opacity: 1;
}
