
.club-tariff {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  gap: 1rem;
  flex-grow: 1;
  padding: 1rem 0.5rem;
  width: 100%;
  height: 100%;

  border-radius: 1.38rem;
  backdrop-filter: blur(100px);
  background: #fff;
  border: 4px solid transparent;

  animation: fadeInUp 0.6s ease-out;

  &--royal {
    border: 4px solid #e9876c;
  }

  &:hover {
    border: 4px solid #e9876c;
  }

  @media (min-width: $sm) {
    padding: 20px 10px;
  }

  @media (min-width: $md) {
    padding: 20px;
  }

  &__tooltip {
    @include buttonReset();
    position: absolute;
    top: 1rem;
    right: 1rem;

    & .icon-info {
      width: 1.25rem;
      height: 1.25rem;
       background-image: url('../img/pages/club-tariffs/icons/icon-info.svg');
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    margin: 0;
    font-weight: 700;

    font-family: $mainFontBold;
    font-size: 1.25rem;
    line-height: 120%;
    text-transform: uppercase;

    color: #da6240;

    @media (min-width: $sm) {
      font-size: 1.69rem;

    }

    & span {
      font-family: $mainFont;
      font-weight: 400;
      font-size: 1.06rem;
      text-transform: lowercase;
      color: #42474D;

      @media (min-width: $sm) {
        font-size: 1.25rem;
      }
    }
  }

  &__price {
    font-family: $mainFontBold2;
    font-weight: 800;
    font-size: 2rem;
    line-height: 130%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #5c6166;

    & span {
      font-family: $mainFont;
      font-weight: 500;
      font-size: 1.12rem;
    }
  }

  &__services {
    display: flex;
    flex-direction: column;
    gap: 10px;

    list-style-type: none;
    padding: 0;
    margin: 0;
    margin-bottom: auto;

  }

  &__service {
    position: relative;
    padding-left: 21px;
    font-weight: 700;
    font-size: 0.8rem;
    line-height: 130%;
    color: #42474D;

    @media(min-width: $xs) {
      font-size: 0.9rem;
    }
    
    @media(min-width: $sm) {
      font-size: 0.94rem;
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-image: url('../img/pages/club-tariffs/icons/icon-check.svg');
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: contain;
      width: 16px;
      height: 16px;
    }

    &--cancel {
      color: #9fa1a3;
      font-weight: 300;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        background-image: url('../img/pages/club-tariffs/icons/icon-cancel.svg');
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: contain;
        width: 16px;
        height: 16px;
      }
    }

    &--bold {
      font-family: $mainFontBold;
      font-weight: 700;
    }

    &-tooltip {
      @include buttonReset();

      & .icon {
        width: 16px;
        height: 16px;
      }
    }

  }

  &__link {
    position: relative;
    display: flex;
    justify-content: center;
    padding: 0.62rem 2rem 0.75rem 0; //  
    font-family: $mainFontBold;
    color: #fff;

    font-weight: 700; //  
    font-size: 1.1rem; //  

    border-radius: 0.75rem;
    text-decoration: none;

    letter-spacing: 0.5px; //  
    background: linear-gradient(176deg, #e9876c 0%, #da6240 76.4%, #db7255 100%);
    transition: all 0.4s ease;
    overflow: hidden;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
        transition: left 0.5s ease;
      }

      &:hover {
        transform: translateY(-3px);
        box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);

        &::before {
          left: 100%;
        }
      }

    @media (min-width: $sm) {
      font-size: 1.06rem;
      padding: 0.62rem 2rem 0.75rem 0;
    }

    @media (min-width: $md) {

      font-size: 1.25rem;
    }

    &:hover {
      opacity: 0.7;
      color: white;


      & span::before {
        right: -100%;
      }
    }

    &--red {
      background: #cd4141;
    }

    & span {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 55%;
        right: -70%;
        transform: translateY(-50%);
        background-image: url('../img/pages/club-tariffs/icons/icon-arrow.svg');
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: contain;
        width: 60%;
        height: 13px;

        transition: right 0.3s ease-in-out;

        @media (min-width: $md) {
          top: 51%;
        }
      }
    }
  }
}

 //  AOS     Swiper
.club-tariff[data-aos].swiper-slide {
  transform: none !important;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
