.page-tariffs__club-gallery {
  position: relative;
  width: 100%;
  border-radius: 0;
  background: var(--bg-color);

  @media (min-width: $breakpoint-md) {
    flex: 0 0 45%;
  }

  & .container {
    display: flex;
    flex-direction: column;
    max-width: 1440px;
    margin: 0 auto;
    padding: 0;
  }

  & .swiper-container {
    overflow: hidden;
  }

  & .swiper-slide {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 100vh;

    background-color: var(--bg-color);
    color: var(--text-color);

    border-radius: 0;

    background-position: center;
    background-size: cover;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to top, var(--bg-color) 5%, transparent 100%);
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      transition: transform 0.5s ease;
    }
  }

  & .slide-gallery__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;

    // @media (min-width: $xl) {
    //   min-height: 800px;
    // }
    // @media (min-width: $xxlg) {
    //   min-height: 1000px;
    // }
  }

  & .slide-gallery__info {
    position: absolute;
    bottom: 40px;
    left: 0;
    width: 100%;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 120%;
    color: var(--text-color);
    text-align:center;

    @media (min-width: $breakpoint-md) {
      text-align: left;
      font-size: 2.5rem;
    }
  }

  & .slide-gallery__title {
    text-align:center;
    padding: 0 20px;
    margin-bottom: 15px;

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 1.19rem;
    text-transform: uppercase;
    text-align: center;
    color: var(--text-color);
    max-width: 1200px;

    @media (min-width: $sm) {
      font-size: 1.75rem;
      padding: 0 40px 0 0;
      margin-bottom: 30px;
    }
    @media (min-width: $xl) {
      font-size: 2.5rem;
      padding: 0 40px 0 0;
      text-align:left;
    }
  }

    & .slide-gallery__text {
      text-align:center;
      padding: 20px;
      background: rgba(255, 255, 255, 0.14);
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 0.88rem;
      line-height: 120%;
      color: var(--text-color);

      @media (min-width: $sm) {
        font-size: 1.25rem;
        padding: 16px 40px;
      }
      @media (min-width: $xl) {
        font-size: 2.5rem;
        padding: 30px 40px;
        max-width: 100%;
        text-align:left;
      }

      & span {
        display: block;
        max-width: 500px;
        margin: 0 auto;

        @media (min-width: $xl) {
          max-width: 1200px;
        }
      }
    }

  & .swiper-button-next,
  & .swiper-button-prev {
    top: 65%;
    color: var(--text-color);
    background: rgba(255, 255, 255, 0.7);
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-top: -30px;
    backdrop-filter: blur(5px);

    @media (min-width: $sm) {
      width: 40px;
      height: 40px;
    }
    @media (min-width: $md) {
      top: 50%;
      width: 50px;
      height: 50px;
    }


    &::after {
      font-size: 14px;
      font-weight: bold;
      color: #4b5052;

      @media (min-width: $sm) {
        font-size: 24px;
      }
    }

    &:hover {
      background: rgba(255, 255, 255, 0.9);
      transform: scale(1.1);
    }

    &.swiper-button-disabled {
      opacity: 0.3;
      pointer-events: none;
    }
  }

  & .swiper-button-prev {
    @media (min-width: $xl) {
      top: 37%;
      left: 120px;
    }
  }
  & .swiper-button-next {
    @media (min-width: $xl) {
      top: 37%;
      right: 120px;
    }
  }

  & .swiper-pagination {
    bottom: 0;
    width: 100%;

    &-bullet {
      background: var(--text-color);
      border-color: var(--text-color);
      opacity: 0.7;
      border-radius: 1.44rem;
      width: 1.31rem;
      height: 0.25rem;

      margin: 0 3px;
      transition: all 0.3s ease;

      @media (min-width: $sm) {
        margin: 0 4px;
        width: 1.31rem;
      }
      @media (min-width: $xl) {
        margin: 0 4px;
        width: 2.75rem;
        height: 0.38rem;
      }

      &-active {
        opacity: 1;
        transform: scale(1.1); //   
        background: var(--text-color); //   
      }
    }
  }
}

.container-nav {
  @media (min-width: $xxlg) {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    min-height: 100%;
  }
}

//
/*     */
@keyframes slideTextIn {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideTextOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-40px);
  }
}

@keyframes slideFade {
  from {
    opacity:0;
    transform: translateY(-40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/*     */
.swiper-slide-active .slide-gallery__title {
  animation: slideTextIn 0.5s forwards;
}

.swiper-slide-active .slide-gallery__text {
  animation: slideTextIn 1s forwards;
}
