:root {
  --advantagesItemSize: 100%;
  --advantagesListGap: 0.625rem;

  @media (min-width: $xs) {
    --advantagesListGap: 1.25rem;
    --advantagesItemSize: calc(100% / 2 - var(--advantagesListGap));
  }
}

.page-tariffs__advantages {
  & .title {
    & span {
      display: block;

      @media (min-width: $sm) {
        display: inline-block
      }
    }
  }

  &__wrap {
    background: url('../img/pages/club-tariffs/about/bg.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding: 40px 1rem;

    @media (min-width: $md) {
      padding: 80px;
    }
  }

  &__list {
    list-style: none;
    margin: 0 auto;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: var(--advantagesListGap);

    max-width: 1280px;
    font-size: 0.94rem;
    line-height: 130%;
    letter-spacing: 0.02em;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    flex-grow: 1;
    padding: 1.38rem;
    font-family: $mainFont;
    border-radius: 0.75rem;
    background: linear-gradient(169deg, #f5f7f8 0%, #e1ebf0 100%);
    width : var(--advantagesItemSize);
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.4);

    @media (min-width: $sm) {
      min-height: 150px;
    }
    @media (min-width: $xl) {
      min-height: auto;
    }

    &-header {
      display: flex;
      flex-direction: row-reverse;
      justify-content: start;
      align-items: center;
      gap:0.75rem;
      margin-right: auto;
      color: #42474c;
    }

    &-title {
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 1.25rem;
      line-height: 130%;
      letter-spacing: 0.02em;
      flex-grow: 0;
      width: calc(100% - 24px - 0.25rem);
    }

    & .icon {
      width: 24px;
      height: 24px;
      flex-grow: 1;
    }

    &-text {
      flex-grow: 1;
      font-weight: 400;
      font-size: 0.9rem;
      line-height: 130%;
      letter-spacing: 0.02em;

      @media (min-width: $xs) {
        font-size: 1.12rem;
      }
      @media (min-width: $md) {
        font-size: 1.25rem;
      }
    }

    &-tooltip {
      @include buttonReset();
    }
  }
}
